import "./App.css";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import React,{useState} from 'react'
import Tasklist from "./components/Tasklist";

function App() {
  const [task,setTask]=useState("")
  const [tlist,settlist]=useState([])

 const handleOnClick=()=>{

  if(task){
    settlist([...tlist,task])
    setTask("")

  }
    
  }


  const handler = (event) => {
    if (event.key === 'e') {
      if(task){
        settlist([...tlist,task])
        setTask("")
    
      }
       
    } 
 };
 const handleOnChange=(e)=>{
 setTask(e.target.value)
    
  }



  const deleteicon=(id)=>{
    const updateditems=tlist.filter((item,ind,tlist)=>{
      return id!==ind;
    
    })
    settlist(updateditems)
    
}

  return (
    <>
    <div className="container-fluid my-5 ">
      <div className="row">
        <div className="col-sm-6 shadow-lg text-white p-3 ms-5 border border-dark">
          <h1 className=" text-center ">To-Do</h1>
          <h5 className=" text-center ">Complete Your Task Today</h5>
          <div className="row">
          <div className="col-sm-15 text-white my-1">
          <div style={{display:"flex"}}>
          <input className="form-control p-1 col-9" style={{width:"70%"}} type="text" onChange={handleOnChange} value={task} placeholder="Enter your task here..." />
          <button className="btn btn-warning mx-2 p-1" style={{width:"20%"}} onClick={handleOnClick} onKeyPress={handler}>Add task</button>
          </div>
          </div>
          </div>
        </div>
      </div>
      <div className="container-fluid text-white col-6 p-2 my-5 ms-4" style={{width:"80%",height:"500px"}}>
    <Tasklist task={task} tlist={tlist} deleteicon={deleteicon}/>
    
    </div>
    </div> 
    
    

    </> 
   
  );
}

export default App;
