import React from 'react'
import DeleteIcon from '@mui/icons-material/Delete';
import "./TaskList.css"
import App from "../App"
import { height } from '@mui/system';


function Tasklist(props) {
    
  
  return (
   
   
    <div className="" style={{width:"100%", height:"100%", overflow: "auto"}}>
    { props.tlist.map((elem,i,tlist)=>{
        return (
            
            <div className='scroll listcss shadow-lg p-2  bg-info bg-opacity-10 border border-info border-start-0 rounded-end' key={i} style={{display:"flex"}}>
            <div style={{width:"90%"}}>
            <p>{i+1+".    "} {elem} </p>
            </div>
            <div style={{width:"10%"}}>
            <DeleteIcon className="icon" onClick={()=>props.deleteicon(i)}/>
            </div>
            </div>
        )
    })}
    
    </div>
  )
}

export default Tasklist